.snake {
    &__title {
        color: $c-main;
        letter-spacing: 0.5rem;
        font-size: 4.2rem;
        line-height: 1;
        align-self: start;
        font-weight: 400;
        background: linear-gradient(90deg, #4e287a,#47439a,#345db8);
        opacity: 0.7;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        color: #000;
    }

    &__head {
        margin-bottom: 3.6rem;

        @include breakpoint-down($mobile-width) {
            margin-bottom: 1.6rem;
        }
    }

    &__progress {
        display: block;
        text-align: center;
        color: $c-main;
        font-size: 1.4rem;
        letter-spacing: 0.5rem;
        line-height: 2.5rem;
        text-transform: uppercase;
    }

    &__wrapper {
        margin: auto;
        display: flex;
        justify-content: center;
        flex-direction: column;
        max-width: 400px;
        padding: 3.5rem 0;
        min-height: 100vh;

        @include breakpoint-down($mobile-width) {
            justify-content: start;
            padding: 1.6rem 0;
        }
    }

    &__board {
        box-shadow: 0.2rem 0.2rem 1.5rem 0 rgba(255, 255, 255, 0.676);
        border-radius: 0.7rem;
    }

    &__navigation {
        margin-top: 3.5rem;
        display: grid;
        justify-content: center;
        grid-template-columns: repeat(3, max-content);
        gap: 4rem;
    }

    &__count {
        display: inline-block;
    }

    &__count-out {
        animation: countOut .5s linear;

        @keyframes countOut {
            100% {
                transform: translateY(-100%);
                opacity: 0;
            }
        }
    }

    &__count-in {
        animation: countIn .5s linear;
        animation-delay: .5s;

        @keyframes countIn {
            0% {
                transform: translateY(100%);
                opacity: 0;
            }
            100% {
                transform: translateY(0);
                opacity: 1;
            }
        }
    }

    &__arrow {
        box-shadow: 0 0 1rem 0 rgb(95, 110, 207);
        width: 6.5rem;
        height: 6.5rem;
        margin: auto;
        border-radius: 0.5rem;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;

        &:first-child {
            grid-column: span 3;
        }

        &:nth-child(2n) {
            margin-top: -4.5rem;
        }

        & > img {
            width: 50%;
        }

        &-top {
            img {
                transform: rotate(-90deg);
            }
        }
        &-left {
            img {
                transform: rotate(180deg);
            }
        }
        &-down {
            img {
                transform: rotate(90deg);
            }
        }
    }
}
